
html,body{height:100%;background-color:#ddd
}

.map{height:calc(100vh - 64px)
}
.map .google-map{height:100%
}

.signup .card-panel{max-width:400px;margin:60px auto
}
.signup h2{font-size:2.4em
}
.signup .feedback{text-transform:capitalize
}

.login .card-panel{max-width:400px;margin:60px auto
}
.login h2{font-size:2.4em
}
.login .feedback{text-transform:capitalize
}

.view-profile .card{margin:60px auto;padding:20px
}
.view-profile h2{margin-top:0;font-size:2em
}
.view-profile .comments{max-height:300px;overflow:auto
}
.view-profile .comments::-webkit-scrollbar{width:5px
}
.view-profile .comments::-webkit-scrollbar-track{background-color:#ddd
}
.view-profile .comments::-webkit-scrollbar-thumb{background-color:#999
}
